.Reasons {
  padding: 0 2rem;
  display: flex;
  gap: 2rem;
}

/* left */
.left-r {
  flex: 1 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  grid: auto rows 1fr;
}
.left-r > img {
  object-fit: cover;
}
.left-r > :nth-child(1) {
  width: 12rem;
  grid-row: 1/3;
  height: 28rem;
}

.left-r > :nth-child(2) {
  width: auto;
  height: 16rem;
  grid-column: 2/4;
}

.left-r > :nth-child(3) {
  width: 14rem;
  grid-column: 2/3;
  grid-row: 2;
}
.left-r > :nth-child(4) {
  width: 10rem;
  grid-column: 3/4;
  grid-row: 2;
  height: 11.2rem;
}

/* right */
.right-r {
  flex: 1 1;
  text-transform: uppercase;
  gap: 1rem;
  display: flex;
  flex-direction: column;
}
.right-r > span {
  font-weight: bold;
  color: var(--orange);
}
.right-r > div {
  color: white;
  font-size: 3rem;
  font-weight: bold;
}

/* details */
.details-r {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.details-r > div {
  display: flex;
  font-size: 1rem;
  gap: 1rem;
}
.details-r > div > img {
  width: 2rem;
  height: 2rem;
}

/* partners */
.partners {
  display: flex;
  gap: 1rem;
}
.partners > img {
  width: 3.5rem;
}

@media screen and (max-width: 768px) {
  .Reasons {
    flex-direction: column;
  }
  .left-r {
    gap: 0.3rem;
    overflow: auto;
    margin-left: 0;
  }
  .left-r > :nth-child(1) {
    width: 7rem;
    height: 17.75rem;
  }
  .left-r > :nth-child(2) {
    width: 14rem;
    height: 11rem;
  }
  .left-r > :nth-child(3) {
    width: 8rem;
  }
  .left-r > :nth-child(4) {
    width: 8rem;
    height: 6.46rem;
  }
}
